import React, { useEffect } from 'react';
import { Redirect, useHistory } from 'react-router-dom';
import { Button } from '@material-ui/core';
import ArrowBackIcon from '@material-ui/icons/ArrowBack';
import { Messages } from '../localization';
import '../styles/profile.scss';
import { Quote } from '../Models/Quote/quote';

interface ClientScreenTitleProps {
    id: string;
    title: string;
    count: number;
    isSkip: boolean;
    notRequiredFieldsTitle?: boolean;
    skipFunction: Function | null;
    quote?: Quote;
}

export function ClientScreenTitleComponent({
    id,
    title,
    count,
    isSkip,
    skipFunction,
    notRequiredFieldsTitle,
    quote
}: ClientScreenTitleProps): JSX.Element {
    const history = useHistory();

    if (quote?.hotelId === null || quote?.hotelId === undefined) {
        return <Redirect to={`/clients/onboarding/${id}`} />;
    }
    return (
        <div>
            <div style={{ display: 'flex' }}>
                <ArrowBackIcon className="back-arrow-icon" onClick={(): void => history.goBack()} />
                <div
                    className="back-arrow-active-content"
                    tabIndex={-1}
                    role="button"
                    onKeyDown={(): void => history.goBack()}
                    onClick={(): void => history.goBack()}>
                    {Messages.returnButtonTitle}
                </div>
            </div>
            <div className="client-profile-screen-container">
                <div className="client-screen-title-container">
                    <div className="profile-screen-main-title">
                        {!quote.isModify
                            ? `${count?.toString().padStart(2, '0')} / ${title}`
                            : title}
                    </div>
                    {isSkip && skipFunction && (
                        <div>
                            <Button
                                className="client-screen-title-skip-button"
                                onClick={(): void => skipFunction()}>
                                {Messages.skipThisStep}
                            </Button>
                        </div>
                    )}
                </div>
                {!notRequiredFieldsTitle ? (
                    <div className="profile-screen-require-field-title">
                        {Messages.requiredFields}
                    </div>
                ) : (
                    ''
                )}
            </div>
        </div>
    );
}
