import React, { useState, useRef, useEffect } from 'react';
import {
    Accordion,
    AccordionSummary,
    AccordionDetails,
    Button,
    IconButton
} from '@material-ui/core';
import ExpandMoreIcon from '@material-ui/icons/ExpandMore';
import AddIcon from '@material-ui/icons/Add';
import { Messages } from '../localization';
import { DeleteModalComponent } from './DeleteModal';
import '../styles/configRestaurant.scss';
import { InputComponent } from './InputComponent';
import uploadIcon from '../assets/upload.svg';

interface AccordionProps {
    count: number;
    details: {
        id?: string | null;
        roomType: string | null;
        quantity: number | null;
        occupancyLimit: number | null;
        description: string | null;
        photoUrl: File | string | null;
        fileName: string | null;
    };
    onDataChange: Function;
    onDeleteRoom: Function;
    loading: boolean;
    showDelete: boolean | null;
}

export function AccommodationAccordionComponent({
    count,
    details,
    onDataChange,
    onDeleteRoom,
    loading,
    showDelete
}: AccordionProps): JSX.Element {
    const [hasImage, setHasImage] = useState(typeof details.photoUrl === 'string');
    const [isDeleteModal, setDeleteModal] = useState(false);
    const [imageName, setImageName] = useState<string | null>();
    const [fileUploadError, setFileUploadError] = useState(false);
    const inputFile = useRef<HTMLInputElement>(null);

    useEffect(() => {
        if (typeof details.photoUrl === 'string') {
            setImageName(details.fileName);
            setHasImage(true);
        }
    }, [details]);

    const onTextFieldChange = (state: string, value: string | number | File): void => {
        onDataChange(count, state, value);
    };

    const triggerImageUpload = (): void => {
        if (inputFile && inputFile.current) {
            inputFile.current.click();
        }
    };

    const handleChange = (event: React.ChangeEvent<HTMLInputElement>): void => {
        if (event.target.files) {
            if (event.target.files.length > 0 && event.target.files[0].type.match('image/')) {
                const fileSize = event.target.files[0].size / 1024 / 1024;
                setImageName(event.target.files[0].name);
                setHasImage(true);
                if (fileSize > 5) {
                    setFileUploadError(true);
                } else {
                    setFileUploadError(false);
                    onTextFieldChange('fileName', event.target.files[0].name);
                }
                onTextFieldChange('photoUrl', event.target.files[0]);
            }
        }
    };

    const addButton = (): JSX.Element => {
        if (hasImage) {
            return (
                <div
                    className="hotel-config-add-photo-button-field"
                    style={fileUploadError ? { marginBottom: '50px' } : { marginBottom: '24px' }}>
                    <div className="photo-field-container">
                        <div className="photo-field-label">{Messages.hotelConfigAddPhotoLabel}</div>
                        <div className="accordion-photo-field-value">{imageName}</div>
                        {fileUploadError && (
                            <div className="upload-field-error">
                                {Messages.maximumFileSizeTitle}
                            </div>
                        )}
                    </div>
                    <IconButton className="image-upload-icon" onClick={triggerImageUpload}>
                        <img src={uploadIcon} alt="imageUpload" />
                    </IconButton>
                    <input
                        type="file"
                        ref={inputFile}
                        onChange={(event: React.ChangeEvent<HTMLInputElement>): void =>
                            handleChange(event)
                        }
                        style={{ display: 'none' }}
                        accept="image/*"
                    />
                </div>
            );
        }
        return (
            <div>
                <Button
                    type="button"
                    className="hotel-config-add-photo-button"
                    variant="outlined"
                    startIcon={<AddIcon />}
                    onClick={triggerImageUpload}>
                    {Messages.hotelConfigAddPhotoButtonTitle}
                </Button>
                <input
                    type="file"
                    ref={inputFile}
                    onChange={(event: React.ChangeEvent<HTMLInputElement>): void =>
                        handleChange(event)
                    }
                    style={{ display: 'none' }}
                    accept="image/*"
                />
            </div>
        );
    };

    const onDeleteRoomData = (): void => {
        if (typeof details.photoUrl === 'string') {
            onDeleteRoom(count, details.id, details.photoUrl);
        } else {
            onDeleteRoom(count, details.id, null);
        }
    };

    useEffect(() => {
        if (showDelete) {
            setDeleteModal(false);
        }
    }, [showDelete]);

    return (
        <div>
            <DeleteModalComponent
                isModelOpen={isDeleteModal}
                setModelOpen={setDeleteModal}
                onSuccess={onDeleteRoomData}
                loading={loading}
            />
            <Accordion
                className="accommodation-screen-container"
                style={{ marginTop: '16px', marginRight: '35px' }}>
                <AccordionSummary
                    expandIcon={<ExpandMoreIcon />}
                    aria-controls="panel1a-content"
                    id="panel1a-header">
                    <div
                        className="restoration-screen-container-title"
                        style={{ paddingTop: '24px' }}>
                        {Messages.formatString(Messages.configAccommodationTitle, count + 1)}
                    </div>
                    <Button
                        className="hotel-accommodation-delete-button"
                        onClick={(): void => setDeleteModal(true)}>
                        {Messages.deleteButtonTitle}
                    </Button>
                </AccordionSummary>
                <AccordionDetails>
                    <div className="input-fields-container">
                        <InputComponent
                            placeholder={Messages.roomTypeTitle}
                            value={details.roomType}
                            type="text"
                            className="input-field"
                            isMultiline={false}
                            onTextChange={(value: string): void =>
                                onTextFieldChange('roomType', value)
                            }
                        />
                        <InputComponent
                            placeholder={Messages.roomConfigQuantity}
                            value={details.quantity}
                            type="number"
                            className="input-field"
                            isMultiline={false}
                            onTextChange={(value: string): void =>
                                onTextFieldChange('quantity', value)
                            }
                        />
                    </div>
                    <InputComponent
                        placeholder={Messages.roomConfigOccupancyLimit}
                        value={details.occupancyLimit}
                        type="number"
                        className="input-field"
                        isMultiline={false}
                        onTextChange={(value: string): void =>
                            onTextFieldChange('occupancyLimit', value)
                        }
                    />
                    <div
                        style={{ marginTop: '50px', marginBottom: '10px' }}
                        className="hotel-config-modal-add-photo">
                        {Messages.descriptionLabel}
                    </div>
                    <InputComponent
                        placeholder={Messages.complusoryDescriptionPlaceholder}
                        value={details.description}
                        type="text"
                        className="text-field"
                        isMultiline
                        onTextChange={(value: string): void =>
                            onTextFieldChange('description', value)
                        }
                    />
                    <div style={{ marginTop: '40px' }} className="hotel-config-modal-add-photo">
                        {Messages.hotelConfigAddPhotoLabel}
                    </div>
                    {addButton()}
                </AccordionDetails>
            </Accordion>
        </div>
    );
}
